import { useEffect } from 'react';
import { Link } from 'react-router-dom';
import Swal from 'sweetalert2';
import { useAuthStore } from '../../hook/useAuthStore';
import { useForm } from '../../hook/useForm';
import './AuthStyles.css'

const registerFormFields = {
    registerName: '',
    registerEmail: '',
    registerPassword: '',
    registerPassword2: '',
}

export const RegisterPage = () => {

    const { startRegister, errorMessage } = useAuthStore();

    const { registerName, registerEmail, registerPassword, registerPassword2, onInputChange: onRegisterInputChange } = useForm(registerFormFields);

    const registerSubmit = (event) => {
        event.preventDefault();
        if (registerPassword !== registerPassword2) {
            Swal.fire('Error en registro', 'Las contraseñas no coinciden', 'error');
            return;
        }
        startRegister({ name: registerName, email: registerEmail, password: registerPassword });
    }

    useEffect(() => {
        if (errorMessage !== undefined) {
            Swal.fire('Error', errorMessage, 'error')
        }

    }, [errorMessage])

    return (
        <div className="box">
            <form onSubmit={registerSubmit} className="form">
                <h1>Create Account</h1>
                <input
                    type="text"
                    className="form-control"
                    placeholder="Nombre"
                    name='registerName'
                    value={registerName}
                    onChange={onRegisterInputChange}
                />
                <input
                    type="email"
                    className="form-control"
                    placeholder="Correo"
                    name='registerEmail'
                    value={registerEmail}
                    onChange={onRegisterInputChange}
                />
                <input
                    type="password"
                    className="form-control"
                    placeholder="Contraseña"
                    name='registerPassword'
                    value={registerPassword}
                    onChange={onRegisterInputChange}
                />
                <input
                    type="password"
                    className="form-control"
                    placeholder="Repita la contraseña"
                    name='registerPassword2'
                    value={registerPassword2}
                    onChange={onRegisterInputChange}
                />
                <input
                    type="submit"
                    className="btnSubmit"
                    value="Crear cuenta" />

                <div className="d-flex mt-2">
                    <div className="w-50 text-center">¿Ya tienes una cuenta?</div>
                    <div className="w-50 text-center links"><Link to="/auth/login">Inicia sesion</Link></div>
                </div>
            </form>
        </div>
    )
}
